@import url(https://fonts.googleapis.com/css2?family=Odibee+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --primary-color: rgb(240, 247, 210);
}

body {
  font-family: "Odibee Sans" sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/**************************NAVBAR ***************************/

.logo {
  width: 5rem;
  height: 4rem;
  padding-left: 20%;
}


/*-------Nav Links-----------*/

nav a.nav-link{
  color: aliceblue;
  font-weight: bolder;
  letter-spacing: 0.0375rem;
  cursor: pointer;
  position:relative;
  z-index: 1;
}


nav a.nav-link:hover
{
  -webkit-transform: scale(1.5);
          transform: scale(1.5); 
}


@media only screen and (max-width: 600px) {
  nav a.nav-link:hover
  {
    -webkit-transform: none;
            transform: none;
  }

}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a{
  color: rgb(240, 247, 210) !important;
  color: var(--primary-color) !important;
  margin-right: 0.75rem;
  margin-left: 0.75rem;
}

.navbar-brand {
  max-width: 17.25rem;
  padding: 0 !important;
  height: 4rem !important;
}

@media (max-width: 600px)
{
  .navbar-brand{
    width: 7rem;
  }
}

/************************** HEADER ***************************/

.header-wraper {
  position: relative;
  background: url(/https://github.com/sumanpatra-123/portfolio-page/static/media/pexels-tranmautritam-326508.20c86249.jpg);
  background-size: cover;
  background-position: center;
  height: 90vh;
}

.main-info{
  display: -webkit-flex;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  text-align: center;
  font-family: "Odibee Sans" sans-serif;
  z-index: 1;
}

.main-info h1 {
  color: rgb(26, 26, 204);
  text-shadow: 0 0 20px #fff, 0 0 30px #ff4da6, 0 0 40px #ff4da6, 0 0 50px #ff4da6, 0 0 60px #ff4da6, 0 0 70px #ff4da6, 0 0 80px #ff4da6;
  text-transform: uppercase;
  font-weight: bolder;
}

@media(max-width: 600px)
{
  .main-info h1 {
    font-size: 1.5rem !important;
  }

  .typed-text{
    font-size: 1.3rem !important;
  }
}

/*----------------Typed-Text------------*/

.typed-text {
  font-size: 2rem;
  color: rgb(84, 106, 11);
  font-weight: bold;
}


/*-------------------Header main offer button ------*/

.btn{
  border: 1px solid rgba(209, 255, 6, 0.796);;
  text-transform: uppercase;
  font-weight: 800;
  border-radius: 0;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  color: rgba(62, 206, 168, 0.911);
  margin: 2rem;
}

.btn:hover{
  text-decoration: none;
}

/*--------------Particles JS-------------------------*/

.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;
}

@media (max-width: 600px) 
  {
    .particles-canvas {
      display: none;
    
    }
}

